html.menuview, 
html.menuview body, 
.main-container, 
.content-wrap {
	overflow: hidden;
	width: 100%;
	height: 100%;
}

.main-container {
	/*background:#d8d8d8;*/
}

.content-wrap {
	overflow-y: scroll;
	-webkit-overflow-scrolling: touch;
	-webkit-transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	position: relative;
}

/* Overlay */
.content-wrap::before {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 10;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,0.3);
	content: '';
	opacity: 0;
	-webkit-transition: opacity 0.3s, -webkit-transform 0s 0.3s;
	transition: opacity 0.3s, transform 0s 0.3s;
	-webkit-transform: translate3d(100%,0,0);
	transform: translate3d(100%,0,0);
}

/* Menu Button */
.menu-button {
	
}

/* Close Button */
.close-button {
	width: 1em;
	height: 1em;
	position: absolute;
	right: 1.5em;
	top: 1em;
	overflow: hidden;
	text-indent: 1em;
	border: none;
	background: transparent;
	color: transparent;
}

.close-button::before,
.close-button::after {
	content: '';
	position: absolute;
	width: 2px;
	height: 100%;
	top: 0;
	left: 50%;
	background: #b8b7ad;
}

.close-button::before {
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
}

.close-button::after {
	-webkit-transform: rotate(-45deg);
	transform: rotate(-45deg);
}

/* Menu */
.menu-wrap {
	position: fixed;
	z-index: 1001;
	width: 319px;
	height:100%;
	overflow-y:scroll;
	background: #141720;
	color: #373a47;
	padding: 20px;
	transform: translate3d(-325px,60px,0);
	transition: transform 0.3s;
}
.menu-wrap>.menu {margin-bottom:40px;}
.menu-wrap .widget .widget-title-wrap {border-bottom-color:#767c8c;}
.menu-wrap .widget .widget-title-wrap::after {background:#bfcae0;}
.menu-wrap .widget .widget-title {color:#fffffd;}
.menu-wrap .widget.widget-categories li {border-bottom-color:#353b4b;}
.menu-wrap .widget.widget-categories li a {color:#7e899d;}
.menu-wrap .widget.widget-categories li a .cat-posts{background:#353b49;color:#fff;}
.menu-wrap .widget.widget-categories li a .cat-posts::after{border-top-color: #353b49;}
.menu-wrap .widget.widget-categories li:hover {border-bottom-color: $nav-link-hover-color;}
.menu-wrap .widget.widget-categories li:hover a {color: #fff;}
.menu-wrap .widget.widget-categories li:hover a .cat-posts {background-color: $nav-link-hover-color;color:#fff;}
.menu-wrap .widget.widget-categories li:hover a .cat-posts::after{border-top-color: $nav-link-hover-color;}
.menu-wrap .widget.widget-categories li:last-child {border-bottom-width:0;}

.menu-wrap .side-menu {margin-left:-20px;margin-right:-20px;}
.menu-wrap .side-menu ul>li {border-bottom: 1px solid #353b4b;}
.menu-wrap .side-menu li>ul{border-top: 1px solid transparent;list-style:none;margin:0;padding:0;height:0;display:none;overflow:hidden;}
.menu-wrap .side-menu i.fa {position:absolute;background:#353b49;border-radius:4px;display:inline-block;font-size:12px;color:#fff;min-width:25px;line-height:19px;text-align:center;padding:3px 8px 2px;right:14px;top:11px;@include transition(.35s);}
.menu-wrap .side-menu li>a {color:#7e899d;display:block;padding:14px;position:relative;}
.menu-wrap .side-menu i.fa-angle-down {background:$nav-link-hover-color;color:#fff;}
.menu-wrap .side-menu li>ul a {padding-left:30px;}
.menu-wrap .side-menu li>ul ul>li>a {padding-left:20px;}
.menu-wrap .side-menu li>ul a {padding-left:30px;}
.menu-wrap .side-menu li:last-child {border-bottom-width:0;}
.menu-wrap .side-menu li.menu-item.active>ul,
.menu-wrap .side-menu li.menu-item.current-menu-item>ul {height:auto;display:block;border-top-color:#353b4b;}
.menu-wrap .side-menu li.menu-item.active>a,
.menu-wrap .side-menu li.menu-item.current-menu-item>a {padding-bottom:10px;}

.affix-top {position: relative;}
.affix {top: 20px;}
.affix, .affix-bottom {width: 213px;}
.affix-bottom {position: absolute;}

.profile {
	display: inline-block;
	line-height: 42px;
	margin-bottom: 1em;
	font-weight: 700;
}

.profile img {
	float: left;
	border-radius: 50%;
	margin-right: 10px;
}

.icon-list a {
	display: block;
	color: #b8b7ad;
	font-weight: 700;
	padding: 0.8em 0.55em;
}

.icon-list a:hover,
.icon-list a:focus {
	color: #c94e50;
}

.icon-list a i {
	opacity: 0.5;
	color: #c94e50;
}

.icon-list a span {
	margin-left: 10px;
}

/* Shown menu */
.show-menu {overflow:hidden;}
.show-menu .menu-wrap {
	position:fixed;
	transform: translate3d(0,60px,0);
	border-radius:0 0 10px 0;
}

.show-menu .content-wrap {
	transform: translate3d(40px,40px,0);
}

.show-menu .content-wrap::before {
	opacity: 1;
	transition: opacity 0.3s;
	transform: translate3d(0,0,0);
}

@media (max-width:320px){
	.menu-wrap {width:320px;
	transform: translate3d(-325px,60px,0);}
}

@media (max-width:640px){
	.menu-wrap {width:340px;
	max-height:100%;top:0;
	overflow:scroll!important;
	transform: translate3d(-345px,60px,0);
	}
}