$normal-padding-horizontal-small	:       8px !default;
$normal-padding-vertical-small		:       8px !default;
$normal-padding-horizontal	:       15px !default;
$normal-padding-vertical	:       15px !default;
$padding-80px				:       80px !default;
$padding-40px				:       40px !default;
$padding-30px				:       30px !default;
$padding-25px				:       25px !default;
$padding-20px				:       20px !default;
$padding-15px       :       15px !default;
$padding-10px       :       10px !default;
$nav-link-color				:		#303030 !default;
$nav-link-hover-color		:		#e37d19 !default;
$nav-border-color			:		#e7e7e7 !default;
$border-color-normal		:		#ececec !default;
$border-color-dark			:		#969696 !default;
$nav-carousel-bg			:		#f8f8f8 !default;

$twitter: #00aced;
$facebook: #3b5998;
$dribbble: #f7679c;
$behance: #0d78ff;
$googleplus: #dd4b39;
$pinterest: #cb2027;
$linkedin: #007bb6;
$tumblr: #32506d;
$instagram: #bc2a8d;
$stumbleupon: #EB4823;
$rss: #FF6600;

@mixin line-clamp($lines) {
	display: -webkit-box;-webkit-line-clamp: $lines;-webkit-box-orient: vertical;overflow: hidden;
}

@mixin transition($trns){
	transition: all $trns ease;
}

*{text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased!important;-moz-osx-font-smoothing:grayscale;outline:none;}
body {color:#252525;overflow-x:hidden;}
a:hover,
a:link {text-decoration:none;}
.img-inline {display:inline-block;}

.required {color:red;}

.list-unstyled {padding:0;margin:0;}

.no-margin {margin:0;}
.no-padding {padding:0;}
.mt-20 {margin-top:$padding-20px;}
.mt-30 {margin-top:$padding-30px;}
.mt-40 {margin-top:$padding-40px;}

.row-5 {margin-left:-5px;margin-right:-5px;}
.row-5 .col {padding-left:5px;padding-right:5px;}
.row-10 {margin-left:-10px;margin-right:-10px;}
.row-10 .col {padding-left:10px;padding-right:10px;}

.line-control {position:relative;margin-top:25px;}
.line-control label {font-weight:normal;position:absolute;top:8px;left:10px;@include transition(.35s);}
.line-control .form-control {border:1px solid #e1e1e1;border-width:0 0 1px;border-radius:0;box-shadow:none;background:#f6f6f6;}
.line-control .form-control:hover,
.line-control .form-control:focus {box-shadow:none;}
.line-control .form-control:focus {border-color:$nav-link-hover-color;}
.line-control.active .form-control+label,
.line-control .form-control:focus+label {display:inline-block;top:-25px;left:0;color:$nav-link-hover-color;}

.line-control.lc-big label {top:14px;left:14px;}

/*
*	SITE HEADER
*/
.iLogo {fill: #303030;width: 261px;}
.iLogoCorporate {fill: #303030;width: 261px;}
.iLogoFooter {fill: #8e929c;width: 261px;}
#site-header {z-index:900;position:relative;border-width:0 0 1px 0;border-color:$nav-border-color;border-radius: 0;margin:0;}
#site-header.affix {position: fixed;background:#fff;top: 0;width: 100%;z-index:8;box-shadow:0 1px 10px 1px rgba(0,0,0,.5);}
#site-header .toggle-categories {float: left;padding: 15px;height: 60px;background:none;border:0;border-right: 1px solid $nav-border-color;margin-right:15px;}
#site-header .toggle-categories .fa {font-size:16px;}
#site-header .toggle-categories span {display:block;font-size:10px;text-transform:uppercase;}

body.corporate_page {}
body.corporate_page #site-header {background:none;position:absolute;left:0;right:0;top:0;border-bottom-width:0;}
body.corporate_page #site-header.affix-top .toggle-categories {background:$nav-link-hover-color;color:#fff;border-right-color:transparent;}
body.corporate_page #site-header.affix-top .nav>li>a {color:#fff;}
body.corporate_page #site-header.affix-top .nav>li.current-menu-item, 
body.corporate_page #site-header.affix-top .nav>li:hover, 
body.corporate_page #site-header.affix-top .nav>li:focus {background:none;color:$nav-link-hover-color;}
body.corporate_page #site-header.affix-top .nav>li.social-nav {border-left-width:0;border-right-width:0;position:relative;}
body.corporate_page #site-header.affix-top .nav>li.social-nav::after,
body.corporate_page #site-header.affix-top .nav>li.social-nav::before {content:'';display:block;background:#fff;height:32px;width:1px;position:absolute;top:50%;margin-top:-16px;}
body.corporate_page #site-header.affix-top .nav>li.social-nav::after  {right:0;}
body.corporate_page #site-header.affix-top .nav>li.social-nav::before {left:0;}
body.corporate_page #site-header.affix {position: fixed;background:#fff;top: 0;width: 100%;z-index:8;}
body.corporate_page #site-header.affix-top .iLogo {fill: #FFFFFF;}
body.corporate_page #site-header .nav>li:first-child>a{border-left-color:transparent;}

body.corporate_page.show-menu {}
body.corporate_page.show-menu .menu-wrap {}

/*Navigation Link*/
#site-header .nav > li {position: static;transition: all .35s ease;}
#site-header .nav > li.menu-item-has-children {position: relative;}
#site-header .nav > li.menu-item-has-children.is-mega-menu {position: static;}
#site-header .nav > li > a {transition: all .35s ease !important;color:$nav-link-color;}
#site-header .nav > li:first-child > a {border-left: 1px solid rgba(255, 255, 255, 0.1);}
#site-header .nav > li.current-menu-item > a,
#site-header .nav > li > a:hover,
#site-header .nav > li:focus > a {color: $nav-link-hover-color;}
#site-header .nav > li.current-menu-item,
#site-header .nav > li:hover,
#site-header .nav > li:focus {background-color: #fff;}


/*Search Bar and Social Link*/
.is-sub-menu {position: absolute;bottom: auto;background-color: #fff;min-width: 180px;display: block;left: 0;z-index: 9997;opacity: 0;visibility: hidden;pointer-events: none;transform: scaleY(0);transition: transform .35s ease-in-out;transform-origin: 100% 0%;padding-top: 10px;padding-bottom: 10px;border:1px solid $nav-border-color;box-shadow:0 1px 10px 1px rgba(0,0,0,.08);}
.sub-sub-menu-wrap.is-sub-menu {left: 100%;top: 0;}
.is-sub-menu.mega-menu-wrap {padding: 0;}
.is-mega-menu .sub-sub-menu-wrap.is-mega-hide {display: none !important;}
.is-sub-menu ul {list-style: none;padding:0;margin:0;}
.is-sub-menu li {position: relative;}
.is-sub-menu li.menu-item {color: #777;letter-spacing: 1px;font-style: normal;font-size: 13px;}
.is-sub-menu li.menu-item a {color:#777;white-space: nowrap;display: block;float: none;padding: 10px 20px;overflow: hidden;line-height: 1;transition: all .35s ease-in-out;}
.is-sub-menu li.menu-item a:hover, .is-sub-menu li.menu-item a:focus {color: $nav-link-hover-color;text-decoration: none;}
.menu-item-has-children > .sub-menu-wrap li.menu-item-has-children > a {padding-right: 40px;}
.menu-item-has-children > .sub-menu-wrap li.menu-item-has-children > a:after {content: "\f105";font-family: FontAwesome;display: block;top: 9px;right: 20px;position: absolute;}
li.menu-item-has-children:hover > .is-sub-menu, .is-mega-menu:hover > .is-sub-menu {opacity: 1;visibility: visible;pointer-events: auto;transform: scaleY(1);}

/*Search Bar and Social Link*/
#site-header .nav>li.social-nav {border-left:1px solid $nav-border-color;border-right:1px solid $nav-border-color;padding:0 8px;}
#site-header .nav>li.social-nav>a {display:inline-block;padding-left:8px;padding-right:8px;}
#site-header .nav>li.search-nav {padding-left:8px;}
#site-header .nav>li.search-nav .is-sub-menu {left: auto;right:0;width:300px;}

.nav-search{display:block;z-index:9999;position:absolute;position:absolute;min-width:320px;background-color:#fff;color:#aaa;border:none;right:0;line-height:1.5;padding:$normal-padding-vertical $normal-padding-horizontal;box-shadow:0 1px 10px 1px rgba(0,0,0,.08);}
.search-form{position:relative;display:block;overflow:hidden}
.search-form .screen-reader-text{display:none}
.search-form input[type="submit"]{position:absolute;right:0;top:0;padding:0;bottom:0;width:90px;display:block;text-align:center}
.search-form .ruby-search-input,
.search-form input[type="search"]{padding-right:110px;border:none;border-bottom:1px solid rgba(0,0,0,.05);padding-left:0}
.nav-search-icon{display:block;padding-left:15px;padding-right:15px;opacity:.7}
.nav-search-icon:hover,
.nav-search-icon:focus{opacity:1}


/*Header Sytle 2*/
#site-header.header-style-2 {border-width:0 0 1px 0;border-color:$nav-border-color;border-radius: 0;margin:0;}
#site-header.header-style-2 .logo-advert {padding-top:$normal-padding-vertical;padding-bottom:$normal-padding-vertical;background:#f8f8f8;}
#site-header.header-style-2 .menu-right {float:right;}
#site-header.header-style-2 .nav-pages-social {border-bottom:1px solid $nav-border-color;}
#site-header.header-style-2 .nav-pages-social .nav > li > a {padding-top:$padding-10px;padding-bottom:$padding-10px;position:relative;}
#site-header.header-style-2 .nav-pages-social .nav > li > a:focus {background:none;}
#site-header.header-style-2 .nav > li.current-menu-item,
#site-header.header-style-2 .nav > li.current-menu-item > a,
#site-header.header-style-2 .nav > li:hover,
#site-header.header-style-2 .nav > li:focus,
#site-header.header-style-2 .nav > li > a:hover {background-color: #fff;}
#site-header.header-style-2 .nav > li.current-menu-item > a,
#site-header.header-style-2 .nav > li > a:hover,
#site-header.header-style-2 .nav > li:focus > a {color: $nav-link-hover-color;}
#site-header.header-style-2 .navbar {margin-bottom:0;box-shadow:none;border-width:0 0 1px 0;}
#site-header.header-style-2 .parent-nav > li > a {color:#505050;font-weight:700;font-size:16px;}
#site-header.header-style-2 .parent-nav > li > a::after {content:'';display:inline-block;width:100%;height:3px;position:absolute;bottom:-2px;left:0;}
#site-header.header-style-2 .parent-nav > li > a:hover::after {background:$nav-border-color;}
#site-header.header-style-2 .parent-nav > li.current-menu-item > a::after {background:$nav-link-hover-color;}
#site-header.header-style-2 .navigation-bar.affix {position: fixed;background:#fff;top: 0;width: 100%;z-index:8;box-shadow:0 1px 10px 1px rgba(0,0,0,.5);}


.top-news-carousel {background-color:$nav-carousel-bg;padding:$normal-padding-horizontal 0;}
.top-news-carousel h4 {font-size:21px;line-height:21px;font-weight:700;color:$nav-link-hover-color;margin:10px 0 3px;}
.top-news-carousel .row {position:relative;}
.top-news-carousel .owl-controls {position:absolute;left:-195px;bottom:0;}
.top-news-carousel .owl-controls .owl-prev,
.top-news-carousel .owl-controls .owl-next {display:inline-block;}
.top-news-carousel .owl-controls .owl-next {margin-left:5px;}
.top-news-carousel .owl-controls .owl-prev:hover,
.top-news-carousel .owl-controls .owl-next:hover {color:$nav-link-hover-color;}

.small-post {overflow:hidden;position:relative;}
.small-post .img-wrap{float:left;width:100px;position:relative;overflow:hidden;}
.small-post .img-wrap img {transition: all 3s ease !important;}
.small-post:hover .img-wrap img {transform: scale(1.2) rotate(-5deg);}
.small-post .post-info{margin-left:112px;}
.small-post .post-info .post-title{font-size:14px;font-weight:700;color:#000;margin:5px 0 4px;}
.small-post .post-info .post-title>a{color:#505050;}
.small-post .post-info .post-title>a:hover{color:#000;}
.small-post .post-info p.meta-info {margin:0;font-size:12px;color:#606060;}

.slider-inner {overflow:hidden;}
.slider-inner img {transition: all 3s ease !important;}
.slider-inner .image-wrap {position:relative;transition: all .35s ease !important;}
.slider-inner .image-wrap::after {position:absolute;width:100%;height:100%;background:#000;opacity:0;content:'';top:0;left:0;transition: all 1.2s ease !important;}
.slider-inner .slide-caption{overflow:hidden;padding:$normal-padding-vertical $normal-padding-vertical-small;
color:#fff;width:100%;float:none;text-align: center;
position:absolute;bottom: 0px; left: 0;opacity:.5;transition:all 1s ease !important;}
.slider-inner .post-info {font-size:12px;}
.post-category-block a,
.slider-inner .post-category a {background:#5e5e5e;color:#fff;display:inline-block;padding:6px 10px 4px;font-size:11px;line-height:15px;letter-spacing:1px;@include transition(1s)}
.post-category-block a:hover,
.slider-inner .post-category a:hover {background:$nav-link-hover-color!important;}
.slider-inner .post-title {font-size:22px;line-height:30px;font-weight:700;margin:5px 0 10px;}
.slider-inner .post-title a {color:#fff;@include line-clamp(2);@include transition(.35s);}
.slider-inner:hover img {transform: scale(1.2) rotate(-5deg);}
.slider-inner:hover .image-wrap::after {opacity:.7; }
.slider-inner:hover .slide-caption {bottom:30%;opacity: 1}
.slider-inner .post-title:hover,
.slider-inner .post-title:hover a {color:$nav-link-hover-color;}

.mPad {padding-top: $padding-40px;padding-bottom: $padding-40px;}
.regular-blog-wrap {padding-top: $padding-40px;padding-bottom: $padding-40px;background:#fff;}
.corporate-padding {padding-top: $padding-80px;padding-bottom: $padding-80px;background:#fff;}
.might-be-missed {padding:80px 0 $padding-40px 0;}

.hasVerticalText {padding-left:40px;position:relative;}
.vertical-text {position:absolute;display: block;left:0;top:-8px;}
.vertical-text span {transform: rotate(-90deg) translateY(0.5em);color:#303030;display: inline-block;font-size: 20px;font-weight: 700;line-height: 0;margin: 0 0 1em;text-transform: uppercase;white-space: nowrap;}
.vertical-text span:after {content: '';display: block;padding-top: 100%;}

.section-heading {padding-bottom:10px;margin-bottom:35px;border-bottom:1px solid #d6d6d6;position:relative;}
.section-heading::before {content:'';height:1px;width:70px;background:#6a6a6a;position:absolute;bottom:-1px;left:0;}
.section-heading .mh {font-size:26px;line-height:32px;margin:0;font-weight:700;}
.section-heading .more-link {font-size:12px;color:#606060;margin-top:5px;}
.section-heading .more-link .fa {font-size:9px;}
.section-heading .more-link:hover {color:$nav-link-hover-color;}

.section-heading.small-heading {}
.section-heading.small-heading .mh {font-size:24px;line-height:24px;}

.heading-with-subtitle {padding-bottom:50px;position:relative;}
.heading-with-subtitle::after {height:1px;width:70px;content:'';display:block;position:absolute;bottom:25px;left:0;background:$nav-link-hover-color;}
.heading-with-subtitle .heading-title {font-size:36px;color:#000;font-weight:300;}
.heading-with-subtitle p {font-size:13px;color:#939494;margin:0;text-transform:uppercase;}

.simple-heading {padding-bottom:20px;position:relative;}
.simple-heading .heading-title {font-size:36px;color:#000;font-weight:300;}
.simple-heading p {font-size:13px;color:#939494;margin:0;text-transform:uppercase;}

.image-with-shade-wrap {padding:70px 0 0 70px;}
.image-with-shade {position:relative;}
.image-with-shade::before,
.image-with-shade::after {position:absolute;content:'';width:400px;height:400px;}
.image-with-shade::before{left:-70px;top:-70px;background:#f1f1f1;}
.image-with-shade::after {left:-50px;top:-50px;border:1px solid #cbcbcb;@include transition(1s);animation: circle2block 2s linear;}
.image-with-shade img {z-index:5;position:relative;border:1px solid transparent;@include transition(.4s);}
.image-with-shade:hover img {box-shadow:0 1px 20px 1px rgba(0,0,0,.1);border-color:#cbcbcb;}

.slider-wrap {position:relative;color:#ffffff;}
.scroll-down {width:104px;position:absolute;bottom:20px;left:50%;margin-left:-52px;z-index:10;text-transform:uppercase;font-weight:300;text-align:center;}
.scroll-down span {display:block;letter-spacing:1px;font-size:12px;}
.scroll-down span.arrow-scroll{background:url(../images/arrow-scroll.png) no-repeat 0 bottom;width:15px;height:46px;margin:0 auto 10px;}
.scroll-down span.mouse {width:30px;height:50px;border-radius:40%;border:1px solid #fff;margin:5px auto 0;position:relative;}
.scroll-down span.mouse::before {position:absolute;width:1px;background:#fff;height:10px;left:50%;top:5px;content:'';}

.entry-meta {color:#606060;font-size:11px;}
.entry-meta a {color:#606060;}
.entry-meta.bordered-view li+li {position:relative;}
.entry-meta.bordered-view li+li:before {position:absolute;height:10px;width:1px;content:'';left:0;top:2px;background:#606060;}

a:hover .fa-facebook {color:$facebook;}
a:hover .fa-twitter {color:$twitter;}
a:hover .fa-google-plus {color:$googleplus;}
a:hover .fa-pinterest {color:$pinterest;}
a:hover .fa-stumbleupon {color:$stumbleupon;}
a:hover .fa-instagram {color:$instagram;}
a:hover .fa-linkedin {color:$linkedin;}
a:hover .fa-rss {color:$rss;}

.social-sharing {width:40px;padding:10px 0;}
.social-sharing ul {margin:0;text-align:center;}
.social-sharing ul li {font-size:10px;}
.social-sharing ul li+li {margin-top:5px;}
.social-sharing ul li a {font-size:12px;}

.social-sharing-list {font-size:14px;margin:0;}
.social-sharing-list li {padding-top:5px;float:left;}
.social-sharing-list ,
.social-sharing-list a {color:#979797;}
.social-sharing-list a:hover .fa-facebook {color:$facebook!important;}
.social-sharing-list a:hover .fa-twitter {color:$twitter!important;}
.social-sharing-list a:hover .fa-google-plus {color:$googleplus!important;}
.social-sharing-list a:hover .fa-pinterest {color:$pinterest!important;}
.social-sharing-list a:hover .fa-stumbleupon {color:$stumbleupon!important;}
.social-sharing-list a:hover .fa-instagram {color:$instagram!important;}
.social-sharing-list a:hover .fa-linkedin {color:$linkedin!important;}
.social-sharing-list a:hover .fa-rss {color:$rss!important;}

.social-sharing-list.long-bar {text-align:center;border:1px solid #d6d6d6;border-width:1px 0;margin:$padding-25px 0;}
.social-sharing-list.long-bar li {padding-top:$padding-15px;padding-bottom:10px;float:none;}
.social-sharing-list.long-bar li .fa {font-size:16px;}

.regular-post {}
.regular-post .post-thumbnails {position:relative;margin:0 0 15px;overflow:hidden;}
.regular-post .post-thumbnails img {transition: all 3s ease !important;}
.regular-post:hover .post-thumbnails img {transform: scale(1.2) rotate(-5deg);}
.regular-post .post-thumbnails .category-block {position:absolute;left:8px;top:10px;}
.regular-post .post-thumbnails .category-block a {display:inline-block;color:#232323;background:#fff;padding:3px 7px 2px;font-size:12px;margin-left:5px;@include transition(.35s);}
.regular-post .post-thumbnails .category-block a:link {text-decoration:none;}
.regular-post .post-thumbnails .category-block a:hover {color:#fff;background-color:$nav-link-hover-color;}

.regular-post .post-thumbnails .social-sharing {position:absolute;top:0;right:0;background:rgba(255,255,255,.15);}
.regular-post .post-thumbnails .social-sharing ul {}
.regular-post .post-thumbnails .social-sharing ul li {color:#fff;}
.regular-post .post-thumbnails .social-sharing ul li a {color:#fff;@include transition(.35s);}
.regular-post .post-thumbnails .social-sharing ul li a:hover .fa {color:$nav-link-hover-color;}
.regular-post .post-thumbnails>a {display:block;}
.regular-post .post-thumbnails .ribbon {position:absolute;z-index:100;left:0;display:inline-block;padding:6px 10px;min-width:175px;background:$nav-link-hover-color;color:#fff;text-transform:uppercase;top:0;font-size:14px;letter-spacing:2px;}

.regular-post .post-info {position:absolute;bottom:0;padding:20px;color:#fff;text-shadow: 1px 1px 6px rgba(24, 24, 25, 0.7);}
.regular-post .post-info .entry-title {font-size:30px;font-weight:700;line-height:35px;margin:0 0 5px;} 
.regular-post .post-info .entry-title a {@include line-clamp(2);@include transition(.35s);}
.regular-post .post-info .entry-title a:hover {text-shadow: 2px 2px 1px rgba(0,0,0,1);}
.by-author, .regular-post .post-info p {font-size:12px;line-height:18px;margin:0;} 
.regular-post .post-info a {color:#fff;}
.by-author a {color:#606060;}

.post-author {@include transition(.35s);display:inline-block;padding:3px 6px;}
.post-author:hover {background:$nav-link-hover-color;text-shadow:none;}
.post-author:hover a {text-shadow:none;}

.regular-post.regular-post-2 {text-align:center;}
.regular-post.regular-post-2 .entry-meta,
.regular-post.regular-post-2 .entry-meta a {color:#fff;}
.regular-post.regular-post-2 .post-thumbnails {margin-bottom:10px;}
.regular-post.regular-post-2 .post-thumbnails>a::after {position:absolute;width:100%;height:100%;background:#000;opacity:.7;content:'';top:0;left:0;transition: all 1.2s ease !important;}
.regular-post.regular-post-2:hover .post-thumbnails::after {opacity:.2;}
.regular-post.regular-post-2:hover .post-info {opacity:1;}
.regular-post.regular-post-2 .post-info {z-index:1;}

.regular-post.regular-post-3 {}
.regular-post.regular-post-3 .entry-meta,
.regular-post.regular-post-3 .entry-meta a {}
.regular-post.regular-post-3 .post-thumbnails {margin-bottom:0;}
.regular-post.regular-post-3 .post-thumbnails+.post-info {position:relative;text-shadow:none;color:#212121;padding-left:65px;}
.regular-post.regular-post-3 .post-thumbnails+.post-info a {color:#9e9e9e;}
.regular-post.regular-post-3 .post-info p {color:#252525;font-size:15px;margin-bottom:$padding-10px;}
.regular-post.regular-post-3 .social-sharing {position:absolute;top:0;left:0;border-right:1px solid #d2d2d2;padding:$padding-20px 0 $padding-10px;}
.regular-post.regular-post-3 .social-sharing ul li+li {margin-top:10px;}
.regular-post.regular-post-3 .social-sharing a {color:#252525!important;}

.category-view .regular-post {margin-top:30px;}




.more-text-link,
.regular-post .read-more {text-transform:uppercase;font-size:14px;color:#9e9e9e;font-weight:700;}
.more-text-link:hover,
.regular-post .read-more:hover {color:$nav-link-hover-color!important;}
.regular-post+.regular-post {margin-top:30px;}

.title-overlay-small {margin-bottom:30px;position:relative;}
